@import "../App.scss";

.splash-page {

  section {

    h1, h2, h3 {
      padding:0;
      margin:0;
    }

  }

  section {
    position:relative;
    -webkit-transform:skew(0, -2.76deg) translateY(calc(50vw/-18));
        -ms-transform:skew(0, -2.76deg) translateY(calc(50vw/-18));
            transform:skew(0, -2.76deg) translateY(calc(50vw/-18));
  }

  section, footer {

    h4 {
      font-weight:normal;
      font-size:24px;
      padding:30px 0 80px 0;
    }

    p {
      padding:10px 0;
      font-size:18px;
      max-width:1200px;
      box-sizing:border-box;
    }

  }

  section > * {
    -webkit-transform: skew(0, 2.76deg) translateY(calc(50vw/18));
        -ms-transform: skew(0, 2.76deg) translateY(calc(50vw/18));
            transform: skew(0, 2.76deg) translateY(calc(50vw/18));
    margin-top: calc(50vw/-12);
  }
      
  section.splash-header {
    background-color:$signature_color;
    height:160px;
    box-shadow:0 0 6px rgba(0,0,0,0.5);
    z-index:1;

    img {
      position:absolute;
      top:15px;
      left:15px;
      width:280px;
    }
  }

  section.deco-image {
    height:430px;
    background-size:cover;
    background-repeat:no-repeat;
    background-position:center 90%;

    h3 {
      position:absolute;
      bottom:75px;
      right:50px;
      color:#fff;
      text-shadow:0 0 8px #000;;
      line-height:140%;
      text-align:right;
      font-size:40px;
      font-family: 'Slabo 27px', serif;
      text-align:right;
    }
  }

  section.deco-image::before {
    background-color:$signature_color;
    opacity:0.15;
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    content:'';
  }

  section.intro {
    padding:120px 80px;

    p.to-database {
      padding-top:80px;

      a  {
        padding:14px 40px 14px 30px;
        border-radius:3px;
        display:inline-block;
        background-color:$signature_color;
        color:#fff !important;
        text-decoration:none !important;
        
      }

      a:hover {
        background-color:lighten($signature_color, 5);
      }

      .anticon {
        padding-right:10px;
      }

    }

  }

  section.the-map {
    height:600px;
    overflow:hidden;

    .leaflet-container {
      position:relative;
      top:-20px;
      height:700px;

      .leaflet-popup-close-button {
        display:none;
      }

      .leaflet-tetrastylon-close-button {
        position:absolute;
        top:6px;
        right:6px;
        font-size:20px;
        cursor:pointer;
        color:rgba(0,0,0,0.2);
      }

      .leaflet-tetrastylon-close-button:hover {
        color:rgba(0,0,0,0.5);
      }

      .leaflet-popup-content-wrapper {
        border-radius:0;
        padding:0;

        .leaflet-popup-content {
          width:400px !important;
          margin:0;
          padding:0;
          display:flex;
          flex-direction:row;
          line-height:200%;

          .popup-thumbnail img {
            width:120px;
            height:120px;
            background-color:rgba($signature_color,.15);
          }

          .popup-body {
            position:relative;
            vertical-align:top;
            padding:10px 30px 10px 10px;

            h1 {
              font-size:17px;
            }

            h2 {
              font-size:15px;
              color:#5d5a5a;
            }

            a {
              position:absolute;
              bottom:10px;
              font-size:15px;
              text-decoration:none !important;
            }
          
          }

        }

      }

    }

  }

  section.team {
    padding:100px 80px;
    height:600px;
    box-sizing:border-box;
  }

  footer {
    background-color:$signature_color_darker;
    padding:15px 25px;
    color:rgba(255,255,255,0.8);
    height:300px;
  }

}